.text{
  h2,h3{
    text-transform: uppercase;
    font-family: $font2;
  }
  h2{
    margin-bottom: .67em;
  }
  h3{
    margin-bottom: .5em;
  }
  li,
  p{
    font-size: 1.125em;
    line-height: 130%;
    color: $gray;
  }
  p{
    @include lastM(0 0 2em);
  }
  img{
    width: 100%;
    display: block;
    margin: 3em 0;
  }
  hr{
    display: block;
    height: 2em;
    opacity: 0;
  }
  ul{
    @include grid100gap(1em);
    li{
      padding-left: 2.22em;
      background-image: url(../img/list-arrow.svg);
      background-repeat: no-repeat;
      background-position: left 0 top .167em;
      background-size: .89em auto;
    }
  }
  .banner-slider{
    margin: 3em 0 0;
    overflow: unset;
    .swiper-container{
      overflow: hidden;
    }
    img{
      margin: 0;
    }
    .custom-pagination{
      margin-top: 1.5em;
      justify-content: unset;
    }
    .swiper-nav{
      margin-left: auto;
    }
  }
}
