.logo{
  display: block;
  width: 9em;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  padding: 1.5em 0;
  .container{
    max-width: 1920px;
    padding: 0 1em;
  }
  &__inner{
    padding: 1.25em 1.5em;
    min-height: 5.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      clip-path: polygon( 100% 0%,0% 0%,0% 86.364%,0.641% 100%,28.846% 100%,29.514% 86.364%,70.486% 86.364%,71.154% 100%,99.359% 100%,100% 86.364%,100% 0% );
      background-color: $yellow;
      z-index: -1;
    }
  }
  &._open{
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
    .burger{
      background: #000;
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.burger{
  width: 3em;
  height: 3em;
  clip-path: polygon( 0% 0%,75% 0%,100% 25%,100% 100%,25% 100%,0% 75%,0% 0% );
  position: relative;
  &__border{
    width: 100%;
    height: auto;
  }
  &__icon{
    @include centerAbsolute;
    width: 1.5em;
    height: auto;
  }
  ._view2{
    opacity: 0;
  }
  &:hover{
    background-color: #000;
    rect{
      fill: #fff;
    }
  }
}

.menu{
  position: absolute;
  right: 0;
  top: 100%;
  margin-top: .75em;
  padding-top: 1.75em;
  backdrop-filter: blur(25px);
  background-color: rgba(0, 0, 0, 0.15);
  border: 1px solid $yellow;
  width: 25em;
  clip-path: polygon(calc(100% - .75em) 0, 100% .75em, 100% 100%, 0 100%, 0 0);
  overflow: hidden;
  color: #FFF;
  font-family: $font2;
  text-transform: uppercase;
  transition: all .4s;
  opacity: 0;
  pointer-events: none;
  &:after{
    content: '';
    position: absolute;
    top: .25em;
    right: -.25em;
    width: 1.25em;
    height: 1px;
    background-color: $yellow;
    transform: rotate(45deg);
  }
  &__links{
    @include grid100gap(.75em);
    padding: 0 1.75em;
  }
  &__link{
    padding: .75em 0;
    position: relative;
    text-transform: uppercase;
    overflow: hidden;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $yellow;
      transition: all .6s;
    }
    &:hover{
      color: #fff;
      &:after{
        transform: translateX(100%);
      }
    }
  }
  &__support{
    margin-top: 5.5em;
    background: rgba(0, 0, 0, 0.30);
    backdrop-filter: blur(25px);
    padding: 1.5em 1.75em;
    .text14{
      margin-bottom: .57em;
      opacity: .5;
    }
  }
}

.header-search{
  position: absolute;
  top: .625em;
  left: 50%;
  transform: translateX(-50%);
  padding-bottom: .25em;
  border-bottom: 1px solid $black;
  width: 36.8125em;
  max-width: 31.5%;
  &__field{
    position: relative;
    input{
      width: 100%;
      height: 3.25em;
      background: #000;
      padding: 0 3em 0 1.56em;
      font-size: 1em;
      line-height: 120%;
      color: #FFF;
      font-family: $font1;
      clip-path: polygon(.75em 0, 100% 0, 100% 100%, 0 100%, 0% .75em);
      &::placeholder{
        color: #FFF;
      }
    }
  }
  &__btn{
    width: 1.5em;
    @include center;
    right: 1em;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    img{
      width: 100%;
      display: block;
    }
  }
  &._type2,
  &._open{
    .search-result{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._type2{
    .search-result{
     ._view1{
       display: none;
     }
      ._view2{
        display: block;
      }
    }
  }
}

.search-result{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 2em;
  padding: 1.75em .875em 0;
  clip-path: polygon(.75em 0, calc(100% - .75em) 0, 100% .75em, 100% 100%, 0 100%, 0 .75em);
  border: 1px solid $yellow;
  width: 48em;
  max-width: calc(100vw - 3em);
  overflow: hidden;
  backdrop-filter: blur(25px);
  background: rgba(0, 0, 0, 0.15);
  color: #fff;
  &:before,
  &:after{
    content: '';
    position: absolute;
    top: .25em;
    width: 1.25em;
    height: 1px;
    background-color: $yellow;
  }
  &:before{
    left: -.25em;
    transform: rotate(-45deg);
  }
  &:after{
    right: -.25em;
    transform: rotate(45deg);
  }
  &__title{
    text-transform: uppercase;
  }
  &__items{
    padding: 0 .875em 1.75em;
    overflow-y: auto;
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    gap: 3em 1.5em;
    align-items: flex-start;
    max-height: calc(100 * var(--vh) - 14em);
    @include scroll;
  }
  ._view2{
    display: none;
  }
}

.search-card,
.product-card{
  &__title{
    margin-top: 1em;
    text-transform: uppercase;
  }
  &__price{
    margin-top: auto;
    padding-top: .75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

