.page-top{
  padding-top: 8.5em;
  &__nav{
    display: flex;
    justify-content: center;
  }
  &__back{
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-family: $font2;
    span{
      letter-spacing: 0.05625em;
    }
    img{
      width: 3.75em;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
  }
  &__title{
    margin-top: 4.25em;
    text-align: center;
  }
}

.checkout{
  &__status{
    margin-top: 1em;
    @include center;
    .badge{
      margin-left: 1em;
    }
  }
  &__grid{
    margin-top: 4.25em;
    display: grid;
    grid-template-columns: calc(100% - 51em) 49.625em;
    gap: 1.375em;
  }
}

.badge{
  padding: 0 .75em;
  height: 2.25em;
  display: flex;
  align-items: center;
  color: $black;
}

.checkout-step{
  padding: 1.5em 0 4.25em;
  border-top: 1px solid rgba(255, 255, 255, 0.20);
  display: grid;
  grid-template-columns: 4.875em calc(100% - 6.375em);
  color: $gray;
  &._finished{
    background-image: url(../img/check.svg);
    background-size: 1.5em auto;
    background-position: right 0 top 1.84em;
    background-repeat: no-repeat;
  }
  &._active{
    color: #fff;
    .checkout-step__num,
    .checkout-step__title{
      color: $yellow;
    }
  }
  &__title{
    text-transform: uppercase;
  }
  &__link{
    margin-top: 2em;
  }
  &__btn{
    .m-btn{
      width: 100%;
    }
    small{
      font-size: .77em;
    }
  }
}

.checkout-product{
  display: grid;
  grid-template-columns: 10.1875em calc(100% - 10.1875em - 1.31em);
  gap: 1.31em;
  margin-top: 2em;
  .text24{
    text-transform: uppercase;
  }
  &__price{
    margin-top: .5em;
  }
}

.checkout-fields{
  margin-top: 2em;
  @include grid100gap(1em);
}

.checkout-req{
  margin-top: 1em;
  &__wrap{
    display: flex;
    align-items: center;
    margin-top: 2em;
  }
  &__img{
    width: 3.25em;
    height: 3.25em;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      clip-path: polygon(.75em 0,100% 0,100% 100%,0 100%,0 .75em);
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__title{
    text-transform: uppercase;
    font-family: $font2;
  }
}


.checkout-info{
  position: sticky;
  top: 1em;
  @include grid100gap(1.5em);
  &__group{
    @include grid100gap(.25em);
  }
  &__item{
    display: flex;
    align-items: center;
  }
  .copy-btn{
    margin-left: .75em;
  }
}

.copy-btn{
  @include center;
  width: 1em;
  flex-shrink: 0;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      stroke: $yellow;
    }
  }
}
