.footer{
  clip-path: polygon( 18.75% 0%,2.5% 0%,0% 6.887%,0% 100%,29.375% 100%,30.026% 98.278%,69.974% 98.278%,70.625% 100%,100% 100%,100% 13.63%,62.5% 13.63%,60.052% 6.887%,60% 6.887%,21.25% 6.887%,18.75% 0% );
  padding: 10em 0 5em;
  background-color: $yellow;
  color: $black;
  font-family: $font2;
  margin-top: 4.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__bottom{
    margin-top: 7em;
    border-top: 1px solid $black;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding-top: 1.5em;
  }
  .text16{
    letter-spacing: 0.05em;
  }
}


.footer-nav{
  display: flex;
  align-items: center;
  &__link{
    overflow: hidden;
    position: relative;
    text-transform: uppercase;
    padding: .375em 0;
    @include lastM(0 2em 0 0);
    &:after{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $black;
      transition: all .4s;
      transform: translateX(-120%);
    }
    &:hover{
      color: $black;
      &:after{
        transform: translateX(0%);
      }
    }
  }
}

.footer-support{
  margin-top: 7.5em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-transform: uppercase;
  .text16{
    margin-bottom: .75em;
  }
  &__link{
    position: relative;
    overflow: hidden;
    padding-bottom: .5em;
    &:after{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $black;
      transition: all .6s;
      transform: translateX(-120%);
    }
    &:hover{
      color: $black;
      &:after{
        transform: translateX(0%);
      }
    }
  }
}

.footer-info{
  opacity: .5;
  text-transform: uppercase;
  @include grid100gap(.25em);
}

.footer-pay{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 1.5em 0 0);
    img{
      max-height: 2.25em;
      max-width: 4.18em;
    }
  }
}
