.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }
    &__bg {
        background: rgba(0, 0, 0, 0.50);
        backdrop-filter: blur(25px);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        backdrop-filter: blur(25px);
        background: $black;
        width: 48em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 2em 4em 2em 2em;
        display: inline-block;
        border: 1px solid $yellow;
        clip-path: polygon(.75em 0, calc(100% - .75em) 0, 100% .75em, 100% 100%, 0 100%, 0 .75em);
        &:before,
        &:after{
            content: '';
            position: absolute;
            top: .25em;
            width: 1.25em;
            height: 1px;
            background-color: $yellow;
        }
        &:before{
            left: -.25em;
            transform: rotate(-45deg);
        }
        &:after{
            right: -.25em;
            transform: rotate(45deg);
        }
    }
    .close-btn{
        position: absolute;
        top: 1em;
        right: 1em;
        width: 2em;
        @include center;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $yellow;
            }
        }
    }
    .text48{
        text-transform: uppercase;
    }
    .text{
        margin-top: 1em;
    }
}


.review-form{
    margin-top: 1.5em;
    @include grid100gap(1em);
    .m-btn{
        width: 100%;
    }
}

.thank-info{
    margin-top: 1.5em;
    @include grid100gap(3em);
    .m-btn{
        width: 100%;
    }
}
