.steam{
  display: grid;
  grid-template-columns: 24em calc(100% - 31.875em);
  gap: 7.875em;
  margin-top: 4.25em;
  &__top{
    padding-top: 1.5em;
    border-top: 1px solid $yellow;
    @include grid100gap(1em);
  }
  &__title{
    text-transform: uppercase;
    color: $yellow;
  }
}

.steam-sidebar{
  position: sticky;
  top: 8em;
  min-height: 20em;
  padding: 1.5em 1.5em 2.25em;
  background-color: $yellow;
  color: $black;
  clip-path: polygon( 16.875% 0%,.75em 0%,0% .75em,0% 100%,1.94em 100%,2.69em calc(100% - .75em),calc(100% - 2.69em) calc(100% - .75em),calc(100% - 1.94em) 100%,100% 100%,100% .75em,calc(100% - .75em) 0% );
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    text-transform: uppercase;
    font-family: $font2;
    @include lastM(0 0 .75em);
    &:hover{
      color: $red;
    }
    span{
      font-size: 1.5em;
      line-height: 110%;
    }
  }
  &__users{
    display: flex;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.25);
    padding: 0 .5em;
    height: 1.8125em;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 130%;
    }
  }
  &__items{
    margin-top: 2em;
  }
}

.steam-stat{
  border-bottom: 1px solid $black;
  padding: 1em 0;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  &:first-child{
    border-top: 1px solid $black;
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__img{
    width: 5em;
    margin-left: 1em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.steam-form{
  margin-top: 2em;
  @include grid100gap(1em);
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 14.75em) 13.75em;
    gap: 1em;
  }
  .product-info__props{
    margin-top: 1em;
  }
  .product-info__prop{
    &:last-child{
      border-bottom: none;
    }
  }
  &__bottom{
    margin-top: 1em;
    @include grid100gap(1.5em);
    .m-btn{
      width: 100%;
    }
  }
}

.pay-way{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 3em / 4));
  gap: 1em;
}

.pay-field{
  input:checked{
    &~.pay-field__inner{
      background-color: transparent;
      &:before,
      &:after{
        opacity: 1;
      }
    }
  }
  &__inner{
    padding: 1.19em;
    @include center;
    height: 6.25em;
    background: rgba(255, 255, 255, 0.15);
    clip-path: polygon(.75em 0,100% 0,100% 100%,0 100%,0 .75em);
    position: relative;
    transition: all .3s;
    &:before,
    &:after{
      opacity: 0;
      transition: all .3s;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      clip-path: polygon(.75em 0,100% 0,100% 100%,0 100%,0 .75em);
      border: 1px solid $yellow;
    }
    &:after{
      content: '';
      position: absolute;
      top: .375em;
      width: 1.25em;
      height: 1px;
      background-color: $yellow;
      left: -.25em;
      transform: rotate(-45deg);
    }
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.reviews{
  padding: 2.75em 0 4.25em;
  &__top{
    padding-top: 1.5em;
    border-top: 1px solid $yellow;
    display: flex;
    justify-content: space-between;
  }
  &__title{
    text-transform: uppercase;
  }
  &__btn{
    &:hover{
      color: $red;
    }
  }
  &__items{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 4.25em;
    .m-btn-hexagon{
      background-color: $yellow;
      color: $black;
      &__wrap:after{
        background-color: $yellow;
      }
      &:hover{
        color: $black;
      }
      &:after{
        background-color: $black;
      }
    }
  }
}

.review-card{
  position: relative;
  padding: 1px;
  &:hover{
    &:after{
      background-color: rgba(255, 255, 255, 0.5);
    }
  }
  &__inner{
    padding: 1.5em;
    min-height: 18.25em;
    display: flex;
    flex-direction: column;
    clip-path: polygon(.75em 0,100% 0,100% 100%,0 100%,0 .75em);
    background: $black;
    position: relative;
    z-index: 2;
  }
  &:after{
    transition: all .3s;
    content: '';
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    clip-path: polygon(.75em 0,100% 0,100% 100%,0 100%,0 .75em);
    background-color: rgba(255, 255, 255, 0.20);
    pointer-events: none;
  }
  &__quote{
    width: 2.5em;
    display: block;
    margin-bottom: .75em;
  }
  &__bottom{
    padding-top: 2em;
    margin-top: auto;
    @include grid100gap(.25em);
  }
  &__name{
    font-size: 1.125em;
    line-height: 110%;
    font-family: $font2;
    letter-spacing: 0.05625em;
    text-transform: uppercase;
  }
}

.steam-success{
  margin-top: 2em;
  .m-btn{
    width: 100%;
  }
  .m-btn-hexagon__wrap{
    margin-top: 2em;
  }
  &__badges{
    display: flex;
    .m-badge{
      margin-left: .5em;
    }
  }
}

.m-badge{
  padding: 0 .75em;
  display: flex;
  align-items: center;
  height: 2.25em;
  background-color: $yellow;
  color: $black;
  span{
    font-size: 1em;
    line-height: 130%;
  }
  &._success{
    background: #10D67A;
  }
  &._error{
    background-color: transparent;
    border: 1px solid #F26154;
    color: #F26154;
  }
}
