.comments,
.requirements,
.other-products{
  margin-top: 6.25em;
  padding-top: 1.5em;
  border-top: 1px solid $yellow;
  .text48{
    text-transform: uppercase;
  }
}

.requirements{
  &__items{
    margin-top: 2em;
    @include grid100gap(3em);
  }
}

.qa-card{
  &._active{
    .qa-card__btn{
      background-color: transparent;
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    text-transform: uppercase;
    font-family: $font2;
  }
  &__btn{
    width: 3em;
    height: 2.25em;
    background-color: $yellow;
    border: 1px solid $yellow;
    position: relative;
    svg{
      @include centerAbsolute;
      width: .625em;
      height: auto;
    }
    ._view2{
      opacity: 0;
    }
  }
  &__hidden{
    display: none;
    padding-top: 1.56em;
  }
  .product-info__props{
    margin: 0;
  }
  .product-info__prop{
    align-items: unset;
    .text16{
      &:first-child{
        min-width: 12em;
      }
      &:last-child{
        text-align: right;
      }
    }
  }
}


.other-products{
  &__items{
    margin-top: 2em;
    @include grid100gap(1.5em);
  }
}

.comments{
  &__top{
    display: flex;
    .text24{
      margin-left: .5em;
      color: $yellow;
    }
  }
  &__items{
    margin-top: 3em;
    @include grid100gap(3em);
  }
}

.new-comment{
  min-height: 9.875em;
  margin-top: 2em;
  background-color: $yellow;
  color: $black;
  padding: 2em 1.5em 1.5em;
  clip-path: polygon(.75em 0%,34% 0.00%,calc(34% + .75em) .75em,calc(66% - .75em) .75em,66% 0%,calc(100% - .75em) 0%,100% .75em,100% 100%,0.00% 100%,0.00% .75em);
  .text24{
    text-transform: uppercase;
  }
  &__wrap{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: 3.25em calc(100% - 4.75em);
    gap: 1.5em;
    .fg{
      input{
        background-color: $yellow;
        color: rgba(0, 0, 0, 1);
        &::placeholder{
          color: rgba(0, 0, 0, 0.50);
        }
        &:focus{
          &~._bg{
            background: #000;
          }
        }
      }
      ._bg{
        background-color: $black;
      }
    }
  }
  &__img{
    height: 3.25em;
    background: $black;
    @include center;
    clip-path: polygon(.75em 0%,100% 0.00%,100% 100%,0 100%,0.00% .75em);
    img{
      width: 1.625em;
    }
  }
  &__auth{
    padding: 1em 0 0 4.75em;
    display: none;
    &-wrap{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .text18{
      text-transform: uppercase;
    }
  }
  &__user{
    .text14{
      color: rgba(0, 0, 0, 0.40);
    }
    &-name{
      display: flex;
      align-items: center;
      margin-top: .25em;
      img{
        margin-right: .5em;
        width: 1.5em;
        flex-shrink: 0;
      }
    }
  }
  &._authed{
    .new-comment__img{
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .new-comment__auth{
      display: block;
    }
  }
}

.socials{
  display: flex;
  &__item{
    @include lastM(0 .5em 0 0);
    width: 3em;
    height: 3em;
    @include center;
    border: 1px solid $black;
    svg{
      width: 1.5em;
      height: auto;
    }
    &:hover{
      background-color: $black;
      path:not(._stroke){
        fill: $yellow;
      }
      ._stroke{
        stroke: $yellow;
      }
    }
  }
}

.comment{
  &__replies{
    margin-top: 2em;
    padding-left: 1.5em;
    border-left: 1px solid rgba(255, 255, 255, 0.20);
    &-items{
      @include grid100gap(3em);
    }
  }
}

.comment-card{
  &__top{
    display: grid;
    grid-template-columns: 3.25em calc(100% - 4.25em);
    gap: 1em;
    align-items: center;
  }
  &__img{
    height: 3.25em;
    background: $black;
    @include center;
    clip-path: polygon(.75em 0%,100% 0.00%,100% 100%,0 100%,0.00% .75em);
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__name{
    text-transform: uppercase;
  }
  &__text{
    margin-top: 1em;
  }
  &__bottom{
    margin-top: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__reply{
    color: $yellow;
    &:hover{
      text-decoration: underline;
    }
  }
  &__answer{
    display: none;
  }
  &__user,
  &__wrap{
    display: flex;
    align-items: center;
  }
  &__user{
    margin-left: 1em;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: 1em;
    }
  }
}

.comment-feedback{
  display: flex;
  align-items: center;
  &__count{
    min-width: 3.625em;
    padding: 0 .25em;
    text-align: center;
  }
  &__btn{
    width: 1.5em;
    height: 1.5em;
    border: 1px solid rgba(255, 255, 255, 0.20);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke-opacity: 1;
      }
    }
  }
}
