.fg {
  position: relative;
  margin: 0;
  ._bg{
    clip-path: polygon(.75em 0,100% 0,100% 100%,0 100%,0 .75em);
    background: rgba(#fff,.2);
    position: absolute;
    top: -1px;
    left: -1px;
    width: calc(100% + 2px);
    height: calc(3.25em + 2px);
    transition: all .3s;
  }
  textarea,
  input,
  &-filled{
    clip-path: polygon(.75em 0,100% 0,100% 100%,0 100%,0 .75em);
    padding: 0 1em;
    height: 3.25em;
    width: 100%;
    background-color: #000;
    position: relative;
    z-index: 2;
  }
  textarea,
  input {
    border: none;
    font-size: 1em;
    line-height: 130%;
    color: #fff;
    &::placeholder {
      color: rgba(255, 255, 255, 0.50);
    }
    &:focus {
      &~._bg{
        background-color: #fff;
      }
    }
  }
  textarea{
    height: 12.5em;
    padding-top: .94em;
    padding-bottom: .94em;
    resize: none;
    &~._bg{
      height: calc(12.5em + 2px);
    }
  }
  &__btn{
    position: absolute;
    top: .94em;
    right: 1em;
    z-index: 2;
    &:hover{
      span{
        text-decoration: underline;
      }
    }
    span{
      transition: all .3s;
    }
  }
  &__text{
    margin-top: .75em;
  }
  &__variants{
    position: absolute;
    top: .6875em;
    right: .75em;
    display: flex;
    align-items: center;
    z-index: 2;
  }
  &__variant{
    padding: 0 .5em;
    height: 1.8125em;
    display: flex;
    align-items: center;
    background: rgba(255, 255, 255, 0.15);
    color: rgba(255, 255, 255, 0.50);
    margin-left: .5em;
    span{
      font-size: .875em;
      line-height: 130%;
    }
    &:hover{
      background: rgba(255, 255, 255, 0.45);
      color: rgba(255, 255, 255, 0.70);
    }
  }
  &._error{
    ._bg{
      background: $primary;
    }
  }
}

.fg-filled{
  display: flex;
  align-items: center;
  &__img{
    width: 2.25em;
    height: 2.25em;
    margin-right: .5em;
    flex-shrink: 0;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }
  }
  .text12,
  .text16{
    line-height: 130%;
  }
  .text12{
    color: rgba(255, 255, 255, 0.50);
  }
}

.fg-select{
  &:after{
    content: '';
    position: absolute;
    width: 1em;
    height: 1em;
    top: 1.125em;
    right: 1em;
    background-image: url(../img/select-arrow.svg);
    @include bgImgSize(100%);
    z-index: 2;
    pointer-events: none;
  }
  input{
    cursor: pointer;
    &::placeholder{
      color: #fff;
    }
    &:focus{
      &~._bg{
        background: $yellow;
      }
    }
  }
  &._open{
    z-index: 4;
    ._bg{
      background: $yellow;
    }
    .fg-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.fg-dropdown{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  top: 100%;
  left: -1px;
  width: calc(100% + 2px);
  border: 1px solid $yellow;
  z-index: 2;
  background: $black;
  padding: 1em 0 1.44em;
  &__items{
    @include grid100gap(.75em);
  }
  &__item{
    display: flex;
    align-items: center;
    padding: 0 1em;
    position: relative;
    &:hover{
      cursor: pointer;
      &:before{
        opacity: 1;
      }
    }
    &._selected{
      &:before{
        opacity: 1;
      }
    }
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .5em;
    }
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;
      height: calc(100% + 1em);
      background: rgba(255, 255, 255, 0.10);
      z-index: -1;
      transition: all .3s;
      opacity: 0;
    }
  }
}

.m-check,
.pay-field{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        border-color: $yellow;
        background-color: $yellow;
        background-size: 1em auto;
      }
    }
  }
  label {
    padding-left: 2.0625em;
    min-height: 1.25em;
    font-size: 1em;
    line-height: 125%;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.2);
      width: 1.25em;
      height: 1.25em;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
      background-image: url(../img/check2.svg);
      @include bgImgSize(0);
    }
  }
  a{
    color: $yellow;
    position: relative;
    z-index: 2;
    &:hover{
      color: $red;
    }
  }
}
