.conf{
  padding: 14.75em 0 3em;
  .text80{
    text-align: center;
  }
  .text{
    padding: 4.25em 12.75em 0 19.12em;
    h3{
      margin-bottom: .75em;
    }
    hr{
      height: 3em;
    }
  }
}

.text{
  ol{
    @include grid100gap(1em);
    @include lastM(0 0 1.25em);
    li{
      padding-left: 3.75em;
      position: relative;
      &:before{
        content: attr(data-num);
        position: absolute;
        top: 0;
        left: 0;
        font-family: $font2;
        color: $yellow;
      }
    }
  }
}
