.main-slider{
  overflow: hidden;
  position: relative;
  color: #fff;
  .swiper{
    &-slide{
      height: $vh;
      background-size: cover;
      background-position: center;
    }
  }
  .custom-pagination{
    position: absolute;
    bottom: 5em;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
  }
}

.main-slide{
  display: flex;
  align-items: flex-end;
  padding-bottom: 11.25em;
  &__inner{
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__info{
    @include grid100gap(1em);
    text-align: center;
  }
  .m-title{
    color: #FFF;
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
    justify-content: center;
  }
}

.custom-pagination{
  max-width: 100%;
  width: 48.6875em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .swiper-pagination{
    position: static;
    margin: 0 2.31em;
    width: auto;
    flex-grow: 1;
    display: flex;
    align-items: center;
    &-bullet{
      opacity: 1;
      background: rgba(255, 255, 255, 0.25);
      height: 1px;
      flex-grow: 1;
      @include lastM(0 .25em 0 0);
      position: relative;
      transition: all .3s;
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%) skewX(-50deg);
        width: 100%;
        height: 5px;
        background-color: rgba(255, 255, 255, 0.25);
        transition: inherit;
        opacity: 0;
      }
      &:hover{
        background-color: transparent;
        &:before{
          opacity: 1;
        }
      }
      &-active{
        background-color: transparent;
        &:before{
          background-color: $yellow;
          opacity: 1;
        }
      }
    }
  }
}

.collection{
  padding: 8.75em 0 7.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.line-title{
  position: relative;
  text-transform: uppercase;
  font-family: $font2;
  &:after{
    content: '';
    position: absolute;
    width: 4.25em;
    height: 0.3125em;
    top: 100%;
    left: 0;
    margin-top: 2em;
    background-color: $yellow;
    transform: skewX(-50deg);
  }
}

.collection-slider{
  margin-top: 7em;
  margin-left: -.5em;
  overflow: hidden;
  padding: .5em;
  width: calc(100% + 1em);
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-slide{
      width: 25%;
      padding-right: 1.25em;
    }
  }
}

