.banners{
  padding: 6.25em 0 4.75em;
  &__img{
    width: 100%;
    display: block;
  }
  .custom-pagination{
    margin: 3.75em auto 0;
  }
}

.banner-slider{
  overflow: hidden;
  .swiper-slide{
    display: block;
  }
}

.popular{
  padding: 8.75em 0 6.25em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .875em));
    gap: 1.75em;
    margin-top: 7.31em;
  }
}

.product-card2{
  position: relative;
  &._big{
    height: 100%;
    grid-row: 1/3;
    grid-column: 1/2;
    .product-card2__img{
      ._view1{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
    .product-card2__content{
      bottom: 4px;
      width: 99.6%;
    }
  }
  &__img{
    height: 100%;
    width: 100%;
    display: block;
    &-inner{
      width: 100%;
      height: 100%;
    }
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
    ._view1{
      display: none;
    }
  }
  &__content{
    position: absolute;
    left: 1px;
    bottom: 1px;
    width: 99.3%;
    padding: 1em 1.5em 1.5em;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    &:before{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      clip-path: polygon(100% 0, 100% calc(100% - .75em), calc(100% - .5em) 100%, 0 100%, 0 0);
      background: #000;
    }
    &:after{
      content: '';
      position: absolute;
      left: 0;
      bottom: 100%;
      width: 100%;
      height: 2.5em;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, #000 100%);
    }
  }
  &__title,
  &__price{
    position: relative;
    z-index: 2;
  }
  &__title{
    text-transform: uppercase;
  }
  &__price{
    white-space: nowrap;
    margin-left: 1em;
    @include grid100gap(.25em);
    text-align: right;
  }
  ._price2{
    text-decoration: line-through;
  }
}

