@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 3.75em;
    text-transform: uppercase;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 110%;
    }
}

.m-btn-hexagon{
    clip-path: polygon(0 0, calc(100% - .75em) 0, 100% .75em, 100% 100%, .75em 100%, 0 calc(100% - .75em));
    position: relative;
    overflow: hidden;
    padding-right: 0;
    &:after{
        content: '';
        align-self: flex-end;
        flex-shrink: 0;
        flex-grow: 1;
        margin-bottom: 1.38em;
        margin-left: auto;
        width: 2.5rem;
        height: 1px;
        background-color: #fff;
        bottom: 1.37em;
        right: 0;
        transition: inherit;
    }
    span{
        margin-right: 1.77em;
        flex-shrink: 0;
    }
    &__wrap{
        position: relative;
        &:after{
            content: '';
            position: absolute;
            top: 100%;
            right: 0;
            width: calc(100% - .75em);
            height: 1px;
            margin-top: .25em;
            background-color: $primary;
        }
        &._black{
            &:after,
            .m-btn{
                background-color: $black;
            }
        }
    }
    &:hover{
        &:after{
            transform: translateX(100%);
        }
    }
}

.m-btn-red{
    justify-content: unset;
    background-color: $primary;
    color: #fff;
    &:hover{
        color: #fff;
    }
}
