@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.m-backdrop{
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, #000 100%);
  }
}

.swiper-nav{
  display: flex;
  align-items: center;
  .swiper-button{
    position: static;
    margin: 0;
    @include lastM(0 2em 0 0);
  }
}

.swiper-button{
  width: 3.75em;
  height: auto;
  transition: all .3s;
  @include center;
  img{
    width: 100%;
    display: block;
    transition: inherit;
  }
  &-prev{
    &:hover{
      img{
        transform: translateX(-1em);
      }
    }
  }
  &-next{
    &:hover{
      img{
        transform: translateX(1em);
      }
    }
  }
}
