.product-page{
  padding-bottom: 6em;
  position: relative;
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    img{
      width: 100%;
      display: block;
    }
    &:after{
      z-index: 2;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.30) 0%, #000 100%);
    }
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__links{
    margin-top: 4.25em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
    gap: .75em;
    border-bottom: .3125em solid $yellow;
  }
  &__link{
    text-transform: uppercase;
    height: 4.5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 1.56em;
    background-color: rgba(0, 0, 0, 0.60);
    backdrop-filter: blur(15px);
    clip-path: polygon( 100% 0%,4.124% 0%,0% 16.667%,0% 100%,100% 100%,100% 0%,90.55% 16.667%,74.399% 16.667%,70.103% 0%,94.845% 0%,90.55% 16.667% );
    &._active{
      background-color: $yellow;
      color: $black;
      backdrop-filter: unset;
    }
    &-price{
      font-weight: 500;
    }
  }
  &__tags{
    margin-top: .5em;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  &__tag{
    margin: .5em .5em 0 0;
    @include center;
    padding: 0 .75em;
    height: 2.25em;
    background: rgba(0, 0, 0, 0.60);
    backdrop-filter: blur(15px);
    color: $gray;
    svg{
      width: 1.5em;
      height: auto;
    }
    &._type2{
      background-color: $yellow;
      backdrop-filter: unset;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 24em calc(100% - 31.87em);
    gap: 7.87em;
    margin-top: 4.25em;
  }
}

.product-info{
  position: sticky;
  top: 8em;
  &__bg{
    width: 100%;
    display: block;
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding: 2em;
    display: flex;
    flex-direction: column;
  }
  &__count{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__props{
    margin-top: 1.5em;
  }
  &__prop{
    padding: .5em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid rgba(255, 255, 255, 0.20);
    &:last-child{
      border-bottom: 1px solid rgba(255, 255, 255, 0.20);
    }
    .text16{
      line-height: 130%;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__prices{
    &-items{
      @include grid100gap(.25em);
    }
    .red-text{
      text-decoration: line-through;
    }
  }
  &__has{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__nav{
    margin-top: 1.5em;
    .m-btn{
      width: 100%;
    }
  }
  &__hint{
    margin-top: 2em;
    color: rgba(255, 255, 255, 0.60);
  }
}

.product-status{
  margin-top: 1.5em;
  display: flex;
  align-items: center;
  &__progress{
    margin-right: 1em;
    flex-shrink: 0;
  }
  &__content{
    @include grid100gap(.25em);
  }
  &__buttons{
    display: flex;
  }
  &__btn{
    display: flex;
    align-items: center;
    @include lastM(0 1em 0 0);
    &:hover{
      opacity: .8;
    }
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.progress{
  position: relative;
  &__chart{
    width: 3em;
    position: relative;
    svg{
      width: 100%;
      height: auto;
    }
    &-fill{
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__num{
    @include centerAbsolute;
  }
}

.gallery{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - .5625em));
  gap: .75em;
  &__item{
    border: 1px solid $yellow;
    position: relative;
    &-img{
      display: block;
      @include imgCover(56.2%);
    }
    &-num{
      @include centerAbsolute;
      text-transform: uppercase;
    }
    &._big{
      grid-column: 1/-1;
    }
    &:nth-child(4)~.gallery__item{
      display: none;
    }
    &:nth-child(5)~._more{
      display: block;
    }
  }
}

.product-advantages{
  margin-top: 1.5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.5em));
  gap: 3em;
  .advantage-card{
    padding: 1.5em 0 !important;
    border: none !important;
    &__icon{
      margin-right: 1em;
    }
    &__link{
      &:hover{
        text-decoration: underline !important;
      }
    }
  }
}

.steam-link{
  display: flex;
  align-items: center;
  svg{
    width: 1.5em;
    height: auto;
    margin-right: .5em;
  }
  &:hover{
    color: $yellow;
    path{
      fill: $yellow;
    }
  }
}

.gift{
  padding: 1.5em 1.5em 2.5em;
  clip-path: polygon( 98.261% 0%,0% 0%,0% 100%,3.478% 100%,5.29% 95.385%,48.333% 95.385%,50.145% 100%,98.261% 100%,100% 95.385%,100% 4.615%,98.261% 0% );
  background-color: $yellow;
  color: $black;
  margin-top: 4.25em;
  &__icon{
    clip-path: polygon( 23.077% 0%,100% 0%,100% 76.923%,76.923% 100%,0% 100%,0% 23.077%,23.077% 0% );
    width: 3.25em;
    height: 3.25em;
    background-color: $black;
    @include center;
    img{
      width: 1.5em;
    }
  }
  &__text{
    margin-top: 1.5em;
    @include grid100gap(1em);
    .text32{
      text-transform: uppercase;
    }
  }
}

.about-game{
  margin-top: 6.25em;
  padding-top: 1.5em;
  border-top: 1px solid $yellow;
  &__nav{
    margin-top: 3em;
    display: flex;
  }
  &__btn{
    display: flex;
    color: $yellow;
    svg{
      width: 1em;
      height: auto;
      flex-shrink: 0;
      margin-left: .75em;
    }
    &:hover{
      color: #fff;
      path{
        stroke: #fff;
      }
    }
  }
}
